import React from 'react';
import './App.css';
// import * as utils from '../utils'

// App
import { Navbar, Card, Button, OverlayTrigger, Tooltip, Modal, Form, Row, Col, InputGroup } from 'react-bootstrap';
// import uuid4 from 'uuid/v4'
// import uuidToHex from 'uuid-to-hex'
import Cookies from 'universal-cookie';
import { GoogleLogin } from 'react-google-login';
// import { GoogleLogin, GoogleLogout } from 'react-google-login';
import FontAwesome from 'react-fontawesome';
import 'font-awesome/css/font-awesome.min.css';

class App extends React.Component {
  constructor(props) {
    super(props);

    // get data from local storage
    let server = window.localStorage.getItem("server");
    if (server == null) {
      server = "http://localhost:5000/";
      window.localStorage.setItem("server", server);
    }
    let endpoint = window.localStorage.getItem("endpoint");
    if (endpoint == null) {
      endpoint = "http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql";
      window.localStorage.setItem("endpoint", endpoint);
    }

    // init app
    this.state = {
      showSettings: false,

      // temp in form
      tempServer: server,
      tempEndpoint: endpoint,
    };
    window.server = server;
    window.endpoint = endpoint;
  }

  render() {
    return (
      <div>
        {this.renderSettings()}

        {/* navbar */}
        <div>
          <Navbar className="shadow" bg="dark" variant="dark" sticky="top" style={{ height: "50px" }}>

            {/* logo */}
            {/* <Navbar.Brand className="pl-2 font-weight-bold" style={{ cursor: "help" }} title="Table to Wikidata Mapping Language">
              T2WML&nbsp;GUI
            </Navbar.Brand> */}
            <OverlayTrigger placement="bottom" trigger="hover"
              // defaultShow="true"
              overlay={
                <Tooltip style={{ width: "fit-content" }}>
                  <div className="text-left">
                    Table to Wikidata<br />
                    Mapping Language
                  </div>
                </Tooltip>
              }
            >
              <Navbar.Brand className="pl-2 font-weight-bold" style={{ cursor: "help" }}>
                T2WML&nbsp;GUI
              </Navbar.Brand>
            </OverlayTrigger>
            <Navbar.Text className="pr-2" style={{ cursor: "default" }}>
              v1.4&nbsp;(beta)
            </Navbar.Text>

            {/* settings */}
            <Navbar.Text className="pr-2 ml-auto" style={{ verticalAlign: "center" }}>
              <Button
                className="d-inline-block float-right"
                variant="outline-light"
                size="sm"
                style={{ padding: "0rem 0.5rem" }}
                onClick={this.handleShowSettings.bind(this)}
              >Settings</Button>
            </Navbar.Text>

          </Navbar>
        </div>

        {/* content */}
        <div style={{ height: "calc(100vh - 50px)", background: "#f8f9fa", paddingTop: "20px" }}>
          <Card className="shadow-sm" style={{ width: "90%", maxWidth: "400px", height: "", margin: "0 auto" }}>
            <Card.Header style={{ height: "40px", padding: "0.5rem 1rem", background: "#343a40" }}>
              <div
                className="text-white font-weight-bold d-inline-block text-truncate"
                style={{ width: "100%", cursor: "default" }}
              >Welcome</div>
            </Card.Header>
            <Card.Body style={{ padding: "10% 10%", overflowY: "auto" }}>

              {/* Visitor */}
              <Button
                variant="outline-dark"
                style={{ width: "100%", marginBottom: "10%" }}
                // onClick={renderProps.onClick}
              >
                <span style={{ width: "90%", display: "inline-block", fontWeight: "bold" }}>Continue as a visitor</span>
              </Button>

              {/* Google */}
              <GoogleLogin
                // clientId="739176097863-e4a0qs78dn9ggbigqgdkr76o3fjvkeli.apps.googleusercontent.com" // @P
                clientId="859571913012-79n4clvbq11q8tifboltfqdvttlh74vr.apps.googleusercontent.com" // @J
                render={renderProps => (
                  <Button
                    variant="success"
                    style={{ width: "100%" }}
                    onClick={renderProps.onClick}
                    disabled={renderProps.disabled}
                  >
                    <FontAwesome name="google" />
                    <span style={{ width: "90%", display: "inline-block", fontWeight: "bold" }}>Log in with Google</span>
                  </Button>
                )}
                buttonText="Login"
                onSuccess={this.onSuccess.bind(this)}
                onFailure={this.onFailure.bind(this)}
                cookiePolicy={'single_host_origin'}
              />

              {/* Splitter */}
              <div style={{ width: "100%", marginTop: "10%" }}>
                <div style={{ display: "inline-block", height: "20px", width: "calc((100% - 120px) / 2)" }}><hr /></div>
                <div style={{ display: "inline-block", height: "20px", width: "120px", textAlign: "center", fontSize: "12px" }} className="text-muted">Not&nbsp;supported&nbsp;yet</div>
                <div style={{ display: "inline-block", height: "20px", width: "calc((100% - 120px) / 2)" }}><hr /></div>
              </div>

              {/* Facebook */}
              <Button
                variant="success"
                style={{ width: "100%", marginTop: "10px" }}
                // onClick={renderProps.onClick}
                disabled={true}
              >
                <FontAwesome name="facebook" />
                <span style={{ width: "90%", display: "inline-block", fontWeight: "bold" }}>Log in with Facebook</span>
              </Button>

              {/* GitHub */}
              <Button
                variant="success"
                style={{ width: "100%", marginTop: "10px" }}
                // onClick={renderProps.onClick}
                disabled={true}
              >
                <FontAwesome name="github" />
                <span style={{ width: "90%", display: "inline-block", fontWeight: "bold" }}>Log in with GitHub</span>
              </Button>

              {/* LinkedIn */}
              <Button
                variant="success"
                style={{ width: "100%", marginTop: "10px" }}
                // onClick={renderProps.onClick}
                disabled={true}
              >
                <FontAwesome name="linkedin" />
                <span style={{ width: "90%", display: "inline-block", fontWeight: "bold" }}>Log in with LinkedIn</span>
              </Button>

              {/* Twitter */}
              <Button
                variant="success"
                style={{ width: "100%", marginTop: "10px" }}
                // onClick={renderProps.onClick}
                disabled={true}
              >
                <FontAwesome name="twitter" />
                <span style={{ width: "90%", display: "inline-block", fontWeight: "bold" }}>Log in with Twitter</span>
              </Button>

            </Card.Body>
          </Card>
        </div>
      </div>
    );
  }

  renderSettings() {
    return (
      <Modal show={this.state.showSettings} size="lg" onHide={() => { /* do nothing */ }}>

        {/* header */}
        <Modal.Header style={{ background: "whitesmoke" }}>
          <Modal.Title>Settings</Modal.Title>
        </Modal.Header>

        {/* body */}
        <Modal.Body>
          <Form className="container">

            {/* project id */}
            <Form.Group as={Row} style={{ marginTop: "1rem" }}>
              <Form.Label column sm="12" md="3" className="text-right">
                Project&nbsp;ID
              </Form.Label>
              {/* <Col sm="12" md="9">
                <Form.Control type="text" defaultValue={window.pid} readOnly />
              </Col> */}
              <Col sm="12" md="9">
                <InputGroup>
                  <Form.Control type="text" defaultValue={window.pid} readOnly />
                  <InputGroup.Append>
                    <Button variant="outline-danger" onClick={() => {
                      this.handleClear();
                      window.location.reload();
                    }}>↻</Button>
                  </InputGroup.Append>
                </InputGroup>
              </Col>
            </Form.Group>

            {/* server */}
            <Form.Group as={Row} onChange={(event) => this.setState({ tempServer: event.target.value })}>
              <Form.Label column sm="12" md="3" className="text-right">
                Server
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control type="url" defaultValue={window.server} />
              </Col>
            </Form.Group>

            {/* query server */}
            <Form.Group as={Row}>
              <Form.Label column sm="12" md="3" className="text-right">
                SPARQL&nbsp;endpoint
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control as="select"
                  onChange={(event) => this.setState({ tempEndpoint: event.target.value })}
                  defaultValue={window.endpoint}
                >
                  <option value="http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql">http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql</option>
                  <option value="http://kg2018a.isi.edu:8888/bigdata/namespace/wdq/sparql">http://kg2018a.isi.edu:8888/bigdata/namespace/wdq/sparql</option>
                  <option value="https://query.wikidata.org/sparql">https://query.wikidata.org/sparql</option>
                </Form.Control>
              </Col>
            </Form.Group>

            {/* cache (for clear qnode cache feature) */}
            {/* <Form.Group as={Row}>
              <Form.Label column sm="12" md="3" className="text-right">
                Cache
              </Form.Label>
              <Col sm="12" md="9">
                <Button variant="secondary" onClick={() => {
                  window.Wikifier.setState({ cacheQnode: {} });
                  alert("Emptied qnode cache.");
                  window.Wikifier.updateCacheQnode();
                }}>
                  Empty qnode cache
                </Button>
              </Col>
            </Form.Group> */}

          </Form>
        </Modal.Body>

        {/* footer */}
        <Modal.Footer style={{ background: "whitesmoke" }}>
          <Button variant="outline-dark" onClick={this.handleHideSettings.bind(this)}>
            Cancel
          </Button>
          <Button variant="dark" onClick={this.handleDoSettings.bind(this)}>
            Save
          </Button>
        </Modal.Footer>

      </Modal>
    );
  }

  onSignIn(googleUser) {
    var profile = googleUser.getBasicProfile();
    console.log('ID: ' + profile.getId()); // Do not send to your backend! Use an ID token instead.
    console.log('Name: ' + profile.getName());
    console.log('Image URL: ' + profile.getImageUrl());
    console.log('Email: ' + profile.getEmail()); // This is null if the 'email' scope is not present.
  }

  onSuccess(googleUser) {
    console.log(googleUser);
    const idToken = googleUser.getAuthResponse().id_token;
    console.log(idToken);
  }

  onFailure(googleUser) {
    console.log(googleUser);
  }

  handleClear() {
    // notify backend
    let formData = new FormData();
    formData.append("id", window.pid);
    const api = window.server + "delete_user";
    fetch(api, {
      body: formData,
      credentials: "same-origin", // "same-origin", "include"
      method: "POST"
    });

    // remove cookie
    const cookies = new Cookies();
    cookies.remove("pid");
  }

  handleDoSettings(event) {
    console.log("<App> updated settings");
    window.server = this.state.tempServer;
    window.localStorage.setItem("server", window.server);
    window.endpoint = this.state.tempEndpoint;
    window.localStorage.setItem("endpoint", window.endpoint);
    this.setState({ showSettings: false });

    // notify backend
    let formData = new FormData();
    formData.append("id", window.pid);
    formData.append("endpoint", window.endpoint);
    const api = window.server + "update_setting";
    fetch(api, {
      body: formData,
      credentials: "same-origin", // "same-origin", "include"
      method: "POST"
    });
  }

  handleHideSettings(event) {
    this.setState({ showSettings: false });
  }

  handleShowSettings(event) {
    this.setState({ showSettings: true });
  }
}

export default App;
